export interface iUser {
  name: string;
}

export interface iAuthState {
  user: null | iUser;
}

export enum AuthActionType {
  LOGIN_USER = "LOGIN_USER",
  LOGIN_USER_REJECTED = "LOGIN_USER_REJECTED",
  LOGIN_USER_FULFILLED = "LOGIN_USER_FULFILLED",
}

export interface iLoginUserAction {
  type: AuthActionType.LOGIN_USER;
}

export interface iLoginUserRejectedAction {
  type: AuthActionType.LOGIN_USER_REJECTED;
}

export interface iLoginUserFulfilledAction {
  type: AuthActionType.LOGIN_USER_FULFILLED;
  payload: iUser;
}

export type AuthAction =
  | iLoginUserAction
  | iLoginUserRejectedAction
  | iLoginUserFulfilledAction;
