export enum ActionStatusType {
  IDLE = "IDLE",
  ACTIVE = "ACTIVE",
  REJECTED = "REJECTED",
  FULFILLED = "FULFILLED",
}

export interface iCallbackOptions {
  onIdleHandler?: () => {};
  onActiveHandler?: () => {};
  onRejectedHandler?: () => {};
  onFulfilledHandler?: () => {};
  onCompletedHandler?: () => {};
}

export enum WatchActionType {
  ADD_ACTION_TO_POOL = "ADD_ACTION_TO_POOL",
  REMOVE_ACTION_FROM_POOL = "REMOVE_ACTION_FROM_POOL",
}

export interface iWatchActionAction {
  type:
    | WatchActionType.ADD_ACTION_TO_POOL
    | WatchActionType.REMOVE_ACTION_FROM_POOL;
  payload: string;
}

export interface iWatchAction {
  status: ActionStatusType;
}

export type iWatchActionState = Record<string, iWatchAction>;
