import { all, put, delay, takeEvery } from "redux-saga/effects";
import { AuthActionType } from "./Auth.types";
import { LoginUserFulfilledAction } from "./Auth.actions";

function* loginUser() {
  // todo: Use the Firebase service
  yield delay(1000);
  yield put(LoginUserFulfilledAction({ name: "Noah" }));
}

function* watchLoginUser() {
  yield takeEvery(AuthActionType.LOGIN_USER, loginUser);
}

export function* AuthSaga() {
  yield all([watchLoginUser()]);
}
