import React, { useEffect, useState } from "react";

export const AsyncComponent = (route: string) => {
  const Component = (props: any) => {
    const [component, setComponent] = useState<null | React.ReactNode>(null);

    useEffect(() => {
      setComponent(<span>Loading...</span>);
      import(`../Components/${route}`)
        .then((c: any) => setTimeout(() => setComponent(c.default), 1000))
        .catch((error: Error) => setComponent(<span>{error.message}</span>));
    }, [route]);

    return (
      <div>
        <h1>Route: {route}</h1>
        <h2>{props.msg}</h2>
      </div>
    );
  };

  return Component;
};
