import {
  AuthActionType,
  iLoginUserAction,
  iLoginUserFulfilledAction,
  iLoginUserRejectedAction,
  iUser,
} from "./Auth.types";

export const LoginUserAction = (): iLoginUserAction => {
  return { type: AuthActionType.LOGIN_USER };
};

export const LoginUserRejectedAction = (): iLoginUserRejectedAction => {
  return { type: AuthActionType.LOGIN_USER_REJECTED };
};

export const LoginUserFulfilledAction = (
  user: iUser
): iLoginUserFulfilledAction => {
  return { type: AuthActionType.LOGIN_USER_FULFILLED, payload: user };
};
