import {
  ActionStatusType,
  iWatchActionAction,
  iWatchActionState,
  WatchActionType,
} from "./useWatchAction.types";

export const WatchActionReducer = (
  state: iWatchActionState = {},
  action: iWatchActionAction
): iWatchActionState => {
  switch (action.type) {
    case WatchActionType.ADD_ACTION_TO_POOL:
      return { ...state, [action.payload]: { status: ActionStatusType.IDLE } };

    case WatchActionType.REMOVE_ACTION_FROM_POOL:
      if (state[action.payload]) {
        delete state[action.payload];
      }
      return state;

    default:
      const actionType: string = action.type as string;
      const watchedActionKey = actionType
        .replace(/_REJECTED$/g, "")
        .replace(/_FULFILLED$/g, "");

      if (state[watchedActionKey]) {
        return processAction(action.type, watchedActionKey, { ...state });
      } else {
        return state;
      }
  }
};

const processAction = (
  type: string,
  watchedActionKey: string,
  state: iWatchActionState
): iWatchActionState => {
  if (type === watchedActionKey) {
    state[watchedActionKey] = {
      status: ActionStatusType.ACTIVE,
    };
  } else if (type === `${watchedActionKey}_REJECTED`) {
    state[watchedActionKey] = {
      status: ActionStatusType.REJECTED,
    };
  } else if (type === `${watchedActionKey}_FULFILLED`) {
    state[watchedActionKey] = {
      status: ActionStatusType.FULFILLED,
    };
  }

  return state;
};
