import { AuthAction, AuthActionType, iAuthState } from "./Auth.types";

const initialState = {
  user: null,
};

export const AuthReducer = (
  state: iAuthState = initialState,
  action: AuthAction
): iAuthState => {
  switch (action.type) {
    case AuthActionType.LOGIN_USER:
    case AuthActionType.LOGIN_USER_REJECTED:
      return { ...state, user: null };

    case AuthActionType.LOGIN_USER_FULFILLED:
      return { ...state, user: action.payload };

    default:
      return state;
  }
};
