import React, { useState, Suspense } from "react";

import { Router } from "@reach/router";
import { useSelector } from "react-redux";

import { LoginView } from "./Views/LoginView/LoginView.component";

import { AsyncComponent } from "./Components/AsyncComponent";

import "./App.css";
import styles from "./App.module.scss";

const Page1 = React.lazy(() => import("./Components/Page1"));
const Page2 = React.lazy(() => import("./Components/Page2"));
const Page3 = React.lazy(() => import("./Components/Page3"));

function App() {
  const state = useSelector((s) => s);

  const [route, setRoute] = useState("Page1");

  const renderPage = (_route: string) => {
    const Component = AsyncComponent(_route);
    return <Component msg={`hello, this is your route: ${_route}`} />;
  };

  const getLoadingDiv = () => {
    return <div>Loading...</div>;
  };

  return (
    <div className="App">
      {/*{renderPage(route)}*/}

      <Suspense fallback={getLoadingDiv()}>
        <Page1 />
      </Suspense>

      <button onClick={() => setRoute("Page1")}>Page 1</button>
      <button onClick={() => setRoute("Page2")}>Page 2</button>
      <button onClick={() => setRoute("Page3")}>Page 3</button>
      <button onClick={() => setRoute("Page4")}>Page 4</button>
    </div>
  );
}

export default App;
